// Frontend JavaScript for Mega Menu Block
document.addEventListener('DOMContentLoaded', function() {
    // Initialize all mega menu blocks on the page
    const megaMenuBlocks = document.querySelectorAll('.adaire-mega-menu');
    
    megaMenuBlocks.forEach(block => {
        initializeMegaMenu(block);
    });
});

function initializeMegaMenu(block) {
    const menuBtn = block.querySelector('.adaire-mega-menu__menu-btn');
    const menuContainer = block.querySelector('.adaire-mega-menu__menu-container');
    const mobileOverlay = block.querySelector('.adaire-mega-menu__mobile-overlay');
    const mobileContent = block.querySelector('.adaire-mega-menu__mobile-content');
    const dropdowns = block.querySelectorAll('.adaire-mega-menu__dropdown-header');
    const subDropdowns = block.querySelectorAll('.adaire-mega-menu__sub-dropdown-header');
    
    let menuOpen = false;
    let activeDropdown = null;
    let navigationStack = []; // Track navigation hierarchy for mobile
    let menuItems = []; // Store menu items data

    // Extract menu items data from the existing structure
    function extractMenuItems() {
        const items = [];
        const dropdownElements = block.querySelectorAll('.adaire-mega-menu__dropdown');
        
        dropdownElements.forEach(dropdown => {
            const header = dropdown.querySelector('.adaire-mega-menu__dropdown-header');
            const menu = dropdown.querySelector('.adaire-mega-menu__menu');
            
            // Extract item regardless of whether it has children
            if (header) {
                const title = header.querySelector('span:not(.adaire-mega-menu__chevron)').textContent;
                const url = header.getAttribute('href') || '#';
                const isBold = header.querySelector('.adaire-mega-menu__bold') !== null;
                
                const item = {
                    id: `item-${items.length}`,
                    title: title,
                    url: url,
                    isBold: isBold,
                    children: []
                };
                
                // Extract tier 2 items only if menu exists
                if (menu) {
                    const subDropdowns = menu.querySelectorAll('.adaire-mega-menu__sub-dropdown');
                    subDropdowns.forEach(subDropdown => {
                        const subHeader = subDropdown.querySelector('.adaire-mega-menu__sub-dropdown-header');
                        const subMenu = subDropdown.querySelector('.adaire-mega-menu__sub-menu');
                        
                        if (subHeader) {
                            const subTitle = subHeader.querySelector('span:not(.adaire-mega-menu__chevron)').textContent;
                            const subUrl = subHeader.getAttribute('href') || '#';
                            const subIsBold = subHeader.querySelector('.adaire-mega-menu__bold') !== null;
                            
                            const subItem = {
                                id: `subitem-${item.id}-${item.children.length}`,
                                title: subTitle,
                                url: subUrl,
                                isBold: subIsBold,
                                children: []
                            };
                            
                            // Extract tier 3 items
                            if (subMenu) {
                                const tier3Links = subMenu.querySelectorAll('li a');
                                tier3Links.forEach(link => {
                                    const tier3Item = {
                                        id: `tier3-${subItem.id}-${subItem.children.length}`,
                                        title: link.textContent,
                                        url: link.getAttribute('href') || '#',
                                        isBold: link.classList.contains('adaire-mega-menu__bold')
                                    };
                                    subItem.children.push(tier3Item);
                                });
                            }
                            
                            item.children.push(subItem);
                        }
                    });
                }
                
                items.push(item);
            }
        });
        
        return items;
    }

    // Mobile menu toggle - attach to entire button element
    if (menuBtn && mobileOverlay && mobileContent) {
        console.log('Mobile menu button found and event listener attached');
        menuBtn.addEventListener('click', (e) => {
            console.log('Mobile menu button clicked!', { menuOpen, target: e.target });
            e.preventDefault();
            e.stopPropagation();
            
            if (!menuOpen) {
                console.log('Opening mobile menu...');
                // Opening menu
                mobileOverlay.classList.add('mobile-overlay-show');
                menuBtn.innerHTML = `<span class="adaire-mega-menu__menu-icon">✕</span>`;
                menuOpen = true;
                
                // Initialize mobile menu to tier 1
                navigationStack = [];
                menuItems = extractMenuItems();
                console.log('Menu items extracted:', menuItems);
                
                // Small delay to allow overlay transition
                setTimeout(() => {
                    renderMobileMenu();
                }, 100);
            } else {
                console.log('Closing mobile menu...');
                // Closing menu
                mobileOverlay.classList.remove('mobile-overlay-show');
                menuBtn.innerHTML = `<span class="adaire-mega-menu__menu-icon">☰</span>`;
                menuOpen = false;
                
                // Clean up mobile menu state and clear content
                navigationStack = [];
                mobileContent.innerHTML = '';
            }
        });
    } else {
        console.log('Mobile menu elements not found:', { menuBtn, mobileOverlay, mobileContent });
    }

    // Render mobile menu based on current navigation level
    function renderMobileMenu() {
        // Get current active tier
        const currentTier = mobileContent.querySelector('.adaire-mobile-menu__tier--active');
        
        if (currentTier) {
            // Determine direction based on current tier and target
            const currentLevel = getCurrentTierLevel();
            const targetLevel = navigationStack.length === 0 ? 1 : navigationStack[navigationStack.length - 1].level;
            const isGoingForward = targetLevel > currentLevel;
            
            console.log('Transition direction:', { currentLevel, targetLevel, isGoingForward });
            
            // Add appropriate exiting class
            if (isGoingForward) {
                currentTier.classList.add('adaire-mobile-menu__tier--exiting-forward');
            } else {
                currentTier.classList.add('adaire-mobile-menu__tier--exiting-backward');
            }
            currentTier.classList.remove('adaire-mobile-menu__tier--active');
            
            // Wait for exit transition, then render new tier
            setTimeout(() => {
                // Remove the exiting tier
                currentTier.remove();
                
                // Render new tier with appropriate entering animation
                if (navigationStack.length === 0) {
                    renderTier1(isGoingForward);
                } else {
                    const currentNav = navigationStack[navigationStack.length - 1];
                    if (currentNav.level === 2) {
                        renderTier2(currentNav.item, isGoingForward);
                    } else if (currentNav.level === 3) {
                        renderTier3(currentNav.item, isGoingForward);
                    }
                }
            }, 300); // Full transition duration
        } else {
            // No current tier, render immediately without animation for initial load
            if (navigationStack.length === 0) {
                renderTier1(false); // Initial load, no animation
            } else {
                const currentNav = navigationStack[navigationStack.length - 1];
                if (currentNav.level === 2) {
                    renderTier2(currentNav.item, true); // Going forward
                } else if (currentNav.level === 3) {
                    renderTier3(currentNav.item, true); // Going forward
                }
            }
        }
    }
    
    // Helper function to get current tier level
    function getCurrentTierLevel() {
        const currentTier = mobileContent.querySelector('.adaire-mobile-menu__tier--active');
        if (!currentTier) return 1;
        
        // Check if it's tier 1 (has close button)
        if (currentTier.querySelector('.adaire-mobile-menu__close-btn')) {
            return 1;
        }
        // Check if it's tier 2 (has back button but no close button)
        if (currentTier.querySelector('.adaire-mobile-menu__back-btn')) {
            return 2;
        }
        // Otherwise it's tier 3
        return 3;
    }
    
    // Render tier 1 (root menu items)
    function renderTier1(isGoingForward = true) {
        const html = `
            <div class="adaire-mobile-menu__tier adaire-mobile-menu__tier--active">
                <div class="adaire-mobile-menu__header">
                    <h3>Menu</h3>
                    <button class="adaire-mobile-menu__close-btn" data-action="close">
                        <span class="adaire-mobile-menu__close-icon">✕</span>
                    </button>
                </div>
                <div class="adaire-mobile-menu__items">
                    ${menuItems.map(item => `
                        <div class="adaire-mobile-menu__item ${item.children.length > 0 ? 'has-children' : ''}" data-item-id="${item.id}">
                            ${item.children.length > 0 ? 
                                `<div class="adaire-mobile-menu__item-content">
                                    <span class="adaire-mobile-menu__item-title ${item.isBold ? 'bold' : ''}">${item.title}</span>
                                    <span class="adaire-mobile-menu__chevron">›</span>
                                </div>` :
                                `<a href="${item.url}" class="adaire-mobile-menu__item-content">
                                    <span class="adaire-mobile-menu__item-title ${item.isBold ? 'bold' : ''}">${item.title}</span>
                                </a>`
                            }
                        </div>
                    `).join('')}
                </div>
            </div>
        `;
        mobileContent.innerHTML = html;
        
        // Apply entering animation based on direction (only if not initial load)
        const newTier = mobileContent.querySelector('.adaire-mobile-menu__tier');
        
        if (isGoingForward !== false) {
            if (isGoingForward) {
                newTier.classList.add('adaire-mobile-menu__tier--entering-forward');
            } else {
                newTier.classList.add('adaire-mobile-menu__tier--entering-backward');
            }
            
            // Remove animation class after animation completes
            setTimeout(() => {
                newTier.classList.remove('adaire-mobile-menu__tier--entering-forward', 'adaire-mobile-menu__tier--entering-backward');
            }, 300);
        }
        
        // Add close button handler
        mobileContent.querySelector('.adaire-mobile-menu__close-btn').addEventListener('click', (e) => {
            e.preventDefault();
            e.stopPropagation();
            console.log('Close button clicked');
            mobileOverlay.classList.remove('mobile-overlay-show');
            menuBtn.innerHTML = `<span class="adaire-mega-menu__menu-icon">☰</span>`;
            menuOpen = false;
            navigationStack = [];
            mobileContent.innerHTML = '';
        });
        
        // Add click handlers for tier 1 items
        mobileContent.querySelectorAll('.adaire-mobile-menu__item').forEach(item => {
            item.addEventListener('click', (e) => {
                console.log('Tier 1 item clicked:', item);
                const itemId = item.getAttribute('data-item-id');
                const menuItem = menuItems.find(mi => mi.id === itemId);
                
                if (menuItem) {
                    if (menuItem.children.length > 0) {
                        // Has children - prevent default and navigate to tier 2
                        e.preventDefault();
                        e.stopPropagation();
                        navigationStack.push({ level: 2, item: menuItem });
                        renderMobileMenu();
                    } else {
                        // No children - allow default anchor behavior (navigation)
                        // Close menu after navigation
                        mobileOverlay.classList.remove('mobile-overlay-show');
                        menuBtn.innerHTML = `<span class="adaire-mega-menu__menu-icon">☰</span>`;
                        menuOpen = false;
                        navigationStack = [];
                        mobileContent.innerHTML = '';
                    }
                }
            });
        });
    }
    
    // Render tier 2
    function renderTier2(parentItem, isGoingForward = true) {
        const html = `
            <div class="adaire-mobile-menu__tier adaire-mobile-menu__tier--active">
                <div class="adaire-mobile-menu__header">
                    <button class="adaire-mobile-menu__back-btn" data-action="back">
                        <span class="adaire-mobile-menu__back-icon">‹</span>
                        <span class="adaire-mobile-menu__back-text">Back</span>
                    </button>
                    <h3>${parentItem.title}</h3>
                </div>
                <div class="adaire-mobile-menu__items">
                    ${parentItem.children.map(item => `
                        <div class="adaire-mobile-menu__item ${item.children.length > 0 ? 'has-children' : ''}" data-item-id="${item.id}">
                            ${item.children.length > 0 ? 
                                `<div class="adaire-mobile-menu__item-content">
                                    <span class="adaire-mobile-menu__item-title ${item.isBold ? 'bold' : ''}">${item.title}</span>
                                    <span class="adaire-mobile-menu__chevron">›</span>
                                </div>` :
                                `<a href="${item.url}" class="adaire-mobile-menu__item-content">
                                    <span class="adaire-mobile-menu__item-title ${item.isBold ? 'bold' : ''}">${item.title}</span>
                                </a>`
                            }
                        </div>
                    `).join('')}
                </div>
            </div>
        `;
        mobileContent.innerHTML = html;
        
        // Apply entering animation based on direction
        const newTier = mobileContent.querySelector('.adaire-mobile-menu__tier');
        
        if (isGoingForward) {
            newTier.classList.add('adaire-mobile-menu__tier--entering-forward');
        } else {
            newTier.classList.add('adaire-mobile-menu__tier--entering-backward');
        }
        
        // Remove animation class after animation completes
        setTimeout(() => {
            newTier.classList.remove('adaire-mobile-menu__tier--entering-forward', 'adaire-mobile-menu__tier--entering-backward');
        }, 300);
        
        // Add back button handler
        mobileContent.querySelector('.adaire-mobile-menu__back-btn').addEventListener('click', (e) => {
            e.preventDefault();
            e.stopPropagation();
            console.log('Back button clicked');
            navigationStack.pop();
            renderMobileMenu();
        });
        
        // Add click handlers for tier 2 items
        mobileContent.querySelectorAll('.adaire-mobile-menu__item').forEach(item => {
            item.addEventListener('click', (e) => {
                console.log('Tier 2 item clicked:', item);
                const itemId = item.getAttribute('data-item-id');
                const menuItem = parentItem.children.find(mi => mi.id === itemId);
                
                if (menuItem) {
                    if (menuItem.children.length > 0) {
                        // Has children - prevent default and navigate to tier 3
                        e.preventDefault();
                        e.stopPropagation();
                        navigationStack.push({ level: 3, item: menuItem });
                        renderMobileMenu();
                    } else {
                        // No children - allow default anchor behavior (navigation)
                        // Close menu after navigation
                        mobileOverlay.classList.remove('mobile-overlay-show');
                        menuBtn.innerHTML = `<span class="adaire-mega-menu__menu-icon">☰</span>`;
                        menuOpen = false;
                        navigationStack = [];
                        mobileContent.innerHTML = '';
                    }
                }
            });
        });
    }
    
    // Render tier 3
    function renderTier3(parentItem, isGoingForward = true) {
        const html = `
            <div class="adaire-mobile-menu__tier adaire-mobile-menu__tier--active">
                <div class="adaire-mobile-menu__header">
                    <button class="adaire-mobile-menu__back-btn" data-action="back">
                        <span class="adaire-mobile-menu__back-icon">‹</span>
                        <span class="adaire-mobile-menu__back-text">Back</span>
                    </button>
                    <h3>${parentItem.title}</h3>
                </div>
                <div class="adaire-mobile-menu__items">
                    ${parentItem.children.map(item => `
                        <div class="adaire-mobile-menu__item" data-item-id="${item.id}">
                            <a href="${item.url}" class="adaire-mobile-menu__item-content">
                                <span class="adaire-mobile-menu__item-title ${item.isBold ? 'bold' : ''}">${item.title}</span>
                            </a>
                        </div>
                    `).join('')}
                </div>
            </div>
        `;
        mobileContent.innerHTML = html;
        
        // Apply entering animation based on direction
        const newTier = mobileContent.querySelector('.adaire-mobile-menu__tier');
        
        if (isGoingForward) {
            newTier.classList.add('adaire-mobile-menu__tier--entering-forward');
        } else {
            newTier.classList.add('adaire-mobile-menu__tier--entering-backward');
        }
        
        // Remove animation class after animation completes
        setTimeout(() => {
            newTier.classList.remove('adaire-mobile-menu__tier--entering-forward', 'adaire-mobile-menu__tier--entering-backward');
        }, 300);
        
        // Add back button handler
        mobileContent.querySelector('.adaire-mobile-menu__back-btn').addEventListener('click', (e) => {
            e.preventDefault();
            e.stopPropagation();
            console.log('Back button clicked (tier 3)');
            navigationStack.pop();
            renderMobileMenu();
        });
    }
    
    // Close mobile menu when clicking outside
    if (mobileOverlay) {
        mobileOverlay.addEventListener('click', (e) => {
            // Only close if clicking on the overlay itself, not on the container or content
            if (e.target === mobileOverlay) {
                console.log('Overlay clicked - closing menu');
                mobileOverlay.classList.remove('mobile-overlay-show');
                menuBtn.innerHTML = `<span class="adaire-mega-menu__menu-icon">☰</span>`;
                menuOpen = false;
                navigationStack = [];
                mobileContent.innerHTML = '';
            }
        });
    }

    // Get the keep canvas open setting
    const keepCanvasOpenOnClick = block.getAttribute('data-keep-canvas-open') === 'true';
    
    // Desktop dropdown functionality - both hover and click
    dropdowns.forEach(dropdown => {
        let isClickControlled = false;
        
        // Click to toggle
        dropdown.addEventListener('click', (e) => {
            const isMobile = window.innerWidth <= 800;
            
            if (isMobile) {
                // Mobile behavior - navigate to tier 2 or allow direct navigation
                const tier2Menu = dropdown.nextElementSibling;
                const hasChildren = tier2Menu && tier2Menu.classList.contains('adaire-mega-menu__menu');
                
                if (hasChildren) {
                    // Only prevent default for items with children (to show submenu)
                    e.preventDefault();
                    e.stopPropagation();
                    navigateToTier2(dropdown, tier2Menu);
                }
                // For items without children, allow default anchor behavior (navigation)
            } else {
                // Desktop behavior - dropdown toggle
                // Only prevent default if clicking on chevron or if keep canvas open is enabled
                const dropdownChevron = dropdown.querySelector('.adaire-mega-menu__chevron');
                const isClickOnChevron = dropdownChevron && dropdownChevron.contains(e.target);
                
                if (isClickOnChevron || keepCanvasOpenOnClick) {
                    e.preventDefault();
                    e.stopPropagation();
                }
            
            isClickControlled = true;
            const menu = dropdown.nextElementSibling;
            const chevron = dropdown.querySelector('.adaire-mega-menu__chevron');
            
            // Close all other dropdowns
            dropdowns.forEach(otherDropdown => {
                if (otherDropdown !== dropdown) {
                    const otherMenu = otherDropdown.nextElementSibling;
                    const otherChevron = otherDropdown.querySelector('.adaire-mega-menu__chevron');
                    
                    if (otherMenu) {
                        otherMenu.classList.remove('menu-show');
                    }
                    if (otherChevron) {
                        otherChevron.classList.remove('icon-rotated');
                    }
                }
            });
            
            // Toggle current dropdown
            if (menu) {
                const isCurrentlyOpen = menu.classList.contains('menu-show');
                
                if (isCurrentlyOpen) {
                    // If keep canvas open is enabled, don't close the dropdown
                    if (!keepCanvasOpenOnClick) {
                        menu.classList.remove('menu-show');
                        activeDropdown = null;
                    }
                } else {
                    menu.classList.add('menu-show');
                    activeDropdown = dropdown;
                }
            }
            
            if (chevron) {
                // If keep canvas open is enabled, don't rotate chevron back
                if (!keepCanvasOpenOnClick) {
                    chevron.classList.toggle('icon-rotated');
                } else {
                    chevron.classList.add('icon-rotated');
                }
                }
            }
        });
        
        // Hover to open (close any click-controlled dropdowns first)
        dropdown.addEventListener('mouseenter', () => {
            // Close any click-controlled dropdowns
            dropdowns.forEach(otherDropdown => {
                if (otherDropdown !== dropdown) {
                    const otherMenu = otherDropdown.nextElementSibling;
                    const otherChevron = otherDropdown.querySelector('.adaire-mega-menu__chevron');
                    
                    if (otherMenu && otherMenu.classList.contains('menu-show')) {
                        otherMenu.classList.remove('menu-show');
                    }
                    if (otherChevron && otherChevron.classList.contains('icon-rotated')) {
                        otherChevron.classList.remove('icon-rotated');
                    }
                }
            });
            
            // Reset active dropdown
            activeDropdown = null;
            
            // Open current dropdown
            const menu = dropdown.nextElementSibling;
            const chevron = dropdown.querySelector('.adaire-mega-menu__chevron');
            
            if (menu) {
                menu.classList.add('menu-show');
            }
            if (chevron) {
                chevron.classList.add('icon-rotated');
            }
        });
        
        // Hover out to close (only if not click controlled and keep canvas open is disabled)
        dropdown.addEventListener('mouseleave', () => {
            if (!isClickControlled && activeDropdown !== dropdown && !keepCanvasOpenOnClick) {
                const menu = dropdown.nextElementSibling;
                const chevron = dropdown.querySelector('.adaire-mega-menu__chevron');
                
                if (menu) {
                    menu.classList.remove('menu-show');
                }
                if (chevron) {
                    chevron.classList.remove('icon-rotated');
                }
            }
        });
        
        // Reset click control when clicking outside
        document.addEventListener('click', () => {
            if (activeDropdown !== dropdown) {
                isClickControlled = false;
            }
        });
    });

    // Sub-dropdown functionality
    subDropdowns.forEach(subDropdown => {
        subDropdown.addEventListener('click', (e) => {
            const isMobile = window.innerWidth <= 800;
            
            if (isMobile) {
                // Mobile behavior - navigate to tier 3
                e.preventDefault();
                e.stopPropagation();
                
                const tier3Menu = subDropdown.nextElementSibling;
                if (tier3Menu && tier3Menu.classList.contains('adaire-mega-menu__sub-menu')) {
                    navigateToTier3(subDropdown, tier3Menu);
                }
            } else {
                // Desktop behavior - toggle sub-menu
            e.preventDefault();
            const subMenu = subDropdown.nextElementSibling;
            const chevron = subDropdown.querySelector('.adaire-mega-menu__chevron');
            
            if (subMenu) {
                subMenu.classList.toggle('sub-menu-show');
            }
            if (chevron) {
                chevron.classList.toggle('icon-rotated');
                }
            }
        });
    });

    // Handle window resize
    window.addEventListener('resize', () => {
        // Close mobile menu when resizing to desktop
        if (window.innerWidth > 800 && menuOpen) {
            mobileOverlay.classList.remove('mobile-overlay-show');
            if (menuBtn) {
                menuBtn.innerHTML = `<span class="adaire-mega-menu__menu-icon">☰</span>`;
                menuOpen = false;
            }
            // Reset navigation stack and clear content
            navigationStack = [];
            mobileContent.innerHTML = '';
        }
    });

    // Close dropdowns when clicking outside
    document.addEventListener('click', (e) => {
        // Close mobile menu when clicking outside
        if (window.innerWidth <= 800 && menuOpen) {
            if (!block.contains(e.target)) {
                mobileOverlay.classList.remove('mobile-overlay-show');
                if (menuBtn) {
                    menuBtn.innerHTML = `<span class="adaire-mega-menu__menu-icon">☰</span>`;
                    menuOpen = false;
                }
                // Reset navigation stack and clear content
                navigationStack = [];
                mobileContent.innerHTML = '';
            }
        }
        
        // Close desktop dropdowns when clicking outside (only if keep canvas open is disabled)
        if (window.innerWidth > 800 && activeDropdown && !keepCanvasOpenOnClick) {
            if (!block.contains(e.target)) {
                const menu = activeDropdown.nextElementSibling;
                const chevron = activeDropdown.querySelector('.adaire-mega-menu__chevron');
                
                if (menu) {
                    menu.classList.remove('menu-show');
                }
                if (chevron) {
                    chevron.classList.remove('icon-rotated');
                }
                activeDropdown = null;
            }
        }
    });

    // Smooth scrolling for anchor links
    const anchorLinks = block.querySelectorAll('a[href^="#"]');
    anchorLinks.forEach(link => {
        link.addEventListener('click', (e) => {
            const href = link.getAttribute('href');
            if (href === '#') {
                e.preventDefault();
                return;
            }
            
            const target = document.querySelector(href);
            if (target) {
                e.preventDefault();
                const offsetTop = target.offsetTop - block.offsetHeight - 20;
                window.scrollTo({
                    top: offsetTop,
                    behavior: 'smooth'
                });
            }
        });
    });

    // Add scroll effect for sticky menu
    const isSticky = block.getAttribute('data-sticky') === 'true';
    if (isSticky) {
        let lastScrollTop = 0;
        const navbar = block.querySelector('.adaire-mega-menu__navbar');
        
        window.addEventListener('scroll', () => {
            const scrollTop = window.pageYOffset || document.documentElement.scrollTop;
            
            if (scrollTop > lastScrollTop && scrollTop > 100) {
                // Scrolling down
                navbar.style.transform = 'translateY(-100%)';
            } else {
                // Scrolling up
                navbar.style.transform = 'translateY(0)';
            }
            
            lastScrollTop = scrollTop;
        });
    }

    // Keyboard navigation
    block.addEventListener('keydown', (e) => {
        if (e.key === 'Escape') {
            const isMobile = window.innerWidth <= 800;
            
            if (isMobile && menuOpen) {
                if (navigationStack.length > 0) {
                    // Go back one level
                    e.preventDefault();
                    navigationStack.pop();
                    renderMobileMenu();
                } else {
                    // Close the menu
                    mobileOverlay.classList.remove('mobile-overlay-show');
                    menuBtn.innerHTML = `<span class="adaire-mega-menu__menu-icon">☰</span>`;
                menuOpen = false;
                    navigationStack = [];
                    mobileContent.innerHTML = '';
                }
            }
        }
    });

    // Add focus management for accessibility
    const focusableElements = block.querySelectorAll('a, button, [tabindex]:not([tabindex="-1"])');
    focusableElements.forEach(element => {
        element.addEventListener('focus', () => {
            element.style.outline = '2px solid var(--mega-menu-hover-color)';
            element.style.outlineOffset = '2px';
        });
        
        element.addEventListener('blur', () => {
            element.style.outline = 'none';
        });
    });
}
